export default {
	name: "tuiListCell",
	props: {
		 //是否有箭头
		arrow: {
			type: Boolean,
			default: false
		},
		//是否有点击效果
		hover: {
			type: Boolean,
			default: true 
		},
		//left 30rpx
		lineLeft:{
			type: Boolean,
			default: true
		},
		//right 30rpx
		lineRight:{
			type: Boolean,
			default: false
		},
		padding:{
			type:String,
			default:"26rpx 30rpx"
		},
		last: {
			type: Boolean,
			default: false //最后一条数据隐藏线条
		},
		radius:{
			type:Boolean,
			default:false
		},
		bgcolor: {
			type: String,
			default: "#fff" //背景颜色
		},
		size: {
			type: Number,
			default: 32 //字体大小
		},
		color: {
			type: String,
			default: "#333" //字体颜色
		},
		index: {
			type: Number,
			default: 0
		}
	},
	methods: {
		handleClick() {
			this.$emit('click', {
				index: this.index
			});
		}
	}
}
